<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">{{ event.pageTicketApplyTitle }}</h1>
          <h2 class="subtitle is-4">
            {{ event.pageTicketApplySubtitle }}
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing">
      <div class="columns is-centered content">
        <div class="column is-6">
          <Markdown :input="event.pageTicketApplyContentMarkdown" />
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="applyForTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">Application Details</h2>
            <div class="content">
              <p>
                This year we have a limited number of tickets available so we
                are using this data to ensure we have a diverse audience rather
                than first come first served.
              </p>
            </div>
            <ApplicationQuestions
              class="section-spacing"
              :details="applicationQuestions"
              :$v="$v"
            />

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="Apply For Ticket"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Markdown from "@/components/Markdown";
import PersonInfo from "@/ticketing/components/PersonInfo";
import ApplicationQuestions from "@/ticketing/components/ApplicationQuestions";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  applicationQuestionsMixin,
  applicationQuestionsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "ApplyForTicket",
  components: {
    Markdown,
    PersonInfo,
    ApplicationQuestions
  },
  mixins: [personQuestionsMixin, applicationQuestionsMixin],
  validations: {
    person: personQuestionsValidations,
    applicationQuestions: applicationQuestionsValidations
  },
  props: {
    event: {
      type: Object,
      required: true
    }
  },
  methods: {
    applyForTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.submitApplication(this.event.slug, {
          person: {
            ...this.person,
            data: this.applicationQuestions
          },
          personDetail: this.applicationQuestions
        })
          .then(res => {
            this.$router.push({
              name: "application-status",
              params: {
                eventSlug: this.event.slug,
                id: res.data.applicationId
              }
            });
            this.person = this.createFreshPerson();
            this.applicationQuestions = this.createFreshApplicationQuestions();
          })
          .catch(e => {
            console.log("ERROR", e);
          });
      }
    },

    createFreshPerson() {
      return {
        firstName: "",
        lastName: "",
        email: "",
        data: {}
      };
    },

    createFreshApplicationQuestions() {
      return {
        age: "",
        gender: "",
        eventsAttended: [],
        occupation: "",
        industry: "",
        educationLevel: ""
      };
    }
  }
};
</script>
